#contact {
    padding: 20px;
    text-align: center;
  }
  
  #contact h2 {
    color: #333;
    margin-bottom: 15px;
  }
  
  #contact p {
    color: #666;
    font-size: 16px;
    line-height: 1.6;
  }
  
  #contact a {
    color: #0066cc;
    text-decoration: none;
  }
  
  #contact a:hover {
    text-decoration: underline;
  }