/* Basic Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  font-family: 'Helvetica Neue', Arial, sans-serif;
  color: #333333;
  line-height: 1.6;
  background: #fff;
  padding: 10px
}

/* Navbar Styles */
nav {
  background-color: #0a0a0a;
  color: white;
  padding: 1rem 2rem;
  position: sticky;
  top: 0;
  z-index: 10;
}

nav ul {
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
}

nav ul li {
  margin: 0 15px;
}

nav ul li a {
  color: white;
  text-decoration: none;
  font-size: 1rem;
  transition: color 0.3s ease;
}

nav ul li a:hover {
  color: #f2a900;
}

/* Section Styles */
section {
  padding: 60px 0;
}

/* Homepage Section */
#home {
  position: relative;
  height: 80vh; /* Adjust as needed */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

#home .background-image-holder {
  position: absolute; /* Will be changed to fixed in desktop view */
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}

#home img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
#home h1, #home p {
  position: relative;
  z-index: 1;
  color: #fff;
  background: rgba(0, 0, 0, 0.5); /* Semi-transparent background for legibility */
  padding: 0.5em 1em;
  border-radius: 5px; /* Optional, for rounded corners */
  margin: 0.5em;
}

#home h1 {
  font-size: 3rem; /* Adjust font size as needed */
}

#home p {
  font-size: 1rem; /* Adjust font size as needed */
  max-width: 600px; /* Ensures text is not too wide */
}



/* Individual Section Styles */
#about, #portfolio, #contact {
  background-color: #eaeaea;
}

#services, #testimonials {
  background-color: #fff;
}








footer {
  background-color: #0a0a0a;
  color: white;
  padding: 1rem;
  margin-top: auto; /* Makes the footer stick to the bottom */
}

footer p {
  margin: 0;
}


/* Responsive Styles */
@media (min-width: 769px) {
  #home .background-image-holder {
    position: fixed; /* Change position for desktop to create a sticky effect */
  }
}

/* Additional Component Styles */
/* Place any additional component-specific styles here */
@media (max-width: 768px) {
  /* Navbar Adjustments */
  nav {
    padding: 0.5rem 1rem;
  }

  nav ul li {
    margin: 0 10px;
  }

  nav ul li a {
    font-size: 0.8rem;
  }

  /* Section and Font Adjustments */
  section {
    padding: 30px 0;
  }

  #home h1 {
    font-size: 2rem;
  }

  #home p,
  #contact p {
    font-size: 1rem;
  }

  /* Footer Adjustments */
  footer {
    padding: 0.5rem;
  }

  /* Adjust image sizes if necessary */
  img {
    max-width: 100%;
    height: auto;
  }

  /* Additional responsive adjustments as needed */
}